	module.exports = {"data":{"post":{"html":"<p>I may watch too much news together with Christopher. He's starting to recognize politicians on TV. Or at least one politician, namely Joschka Fischer. Last week ZDF was showing live speeches of the main candidates, and I was naming them for Christopher. Look, there's Edi. And there's Gerd. And Angie. And there's Joschi! Fischer does have a friendly figure on television, and his name and face have stuck with my son. At least I can give him a reason to watch the news with me instead of his Kleine Eisbär video. We might get to see Joschi!</p>\n<p>BTW, my regular readers might have missed the oodles of <a href=\"http://www.papascott.de/cgi-bin/mt-comments.cgi?entry_id=1903\">comments</a> about German-American relations after my rant about Schröder was linked to by <a href=\"http://www.denbeste.nu/\">USS Clueless</a>.</p>","fields":{"title":"Joschi","date":"25 Sep 2002","url":"https://papascott.de/2002-09-25-joschi/"}}},"pathContext":{"slug":"/2002-09-25-joschi/","prev":{"fields":{"title":"Where Did The Time Go?","date":"24 Sep 2002","slug":"/2002-09-24-where-did-the-time-go/"}},"next":{"fields":{"title":"It's Over Over There","date":"26 Sep 2002","slug":"/2002-09-26-its-over-over-there/"}}}}

